.cards{
    height:21rem;
    position:absolute;
    display:flex;
    flex-direction: column;
    gap:1rem;
    align-items: center;
    text-align: center;
    width: 15rem;
    background:rgba(255 255 255 0.26);
    border : 7px solid var(--orangeCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    margin-bottom: 2rem;
}

img{
    height:60%;
    width:100%;
    border-top-right-radius:10px ;
    border-top-left-radius: 10px;
    /* transform:scale(0.2) */
}
.c-button{
 background: white;
 box-shadow: 0px 19px 60px rgba(0 0 0 0.08);
 border-radius: 7px;
 font-size: 16px;
 color:#5290fd;
 padding-bottom:10px;
 cursor: pointer;
 height: 1rem;
}
.card span:nth-of-type(2){
    color:var(--gray);
    font-size:16px
}
.stack{
    font-size:small;
}
