.n-wrapper{
    height : 12vh;
    display: flex;
    width:94%;
    padding-left:3%;
    padding-right: 3%;
    margin-left: -4%;
    margin-top: -0.6%;
    justify-content: space-between;
    position:fixed;
    gap:20rem;
    z-index: 15;
    border-bottom: 1px solid black;
    background-color:  rgba(255, 255, 255, 0.906);
}
.n-left{
    display : flex;
   flex : 1;
   align-items: center;
   gap : 10rem;
}
.n-name{
    font-weight: bold;
    font-size: 1.3rem;
}
.n-right{
    display: flex;
    flex : 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}
.n-list{
    flex:10;
}
.n-list>ul{
    display: flex;
    gap : 4rem;
    margin-left: -20rem;
    cursor: pointer;
}

.n-list>ul>li:hover{
    cursor: pointer;
    color : var(--orange)
}
.n-button{
    flex : 2
}
/* @media screen and (max-width : 480px){
    .n-list{
        display:none
    }
} */
@media screen and (max-width : 1180px){
    .n-list{
        display:none
    }
    
}