.works{
    margin-top: 4rem;
    display: flex;
    height:30rem;
    padding: 0 3rem 0 3rem;
    position:relative
}
.w-wright{
    position: relative;
    left:20rem
}
.w-maincircle{
 left:13rem;
 position:relative;
 height:18rem;
 width:18rem;
 border-radius: 100%;
 box-shadow: var(--smboxShadow);
 top:2rem;
 background: white;
 z-index: 5;
}
.w-maincircle>*{
    position:absolute;
    z-index: 5;
}
.w-maincircle> :nth-child(1){
    top: -3rem;
    left : 6rem
}
.w-maincircle> :nth-child(2){
    top: 5rem;
    left : -3rem
}
.w-maincircle> :nth-child(3){
    top: 5rem;
    left : 6rem
}
.w-maincircle> :nth-child(4){
    top: 5rem;
    left : 15rem
}
.w-maincircle> :nth-child(5){
    top: 13rem;
    left : 6rem
}
.w-secCircle{
    width:7rem;
    height:7rem;
    position:absolute;
    border-radius: 100%;
    border:1px solid rgb(205, 202, 202);
    display:flex;
    align-items: center;
    justify-content: center;
    box-shadow: var(--smboxShadow);
    background: white;
    z-index: 5;
}
.w-secCircle>img{
    transform: scaleX(0.7);
}
.b-circle{
    position: absolute;
    height:13rem;
    width:13rem;
    border-radius: 100%;
    z-index:1;
}
.blueCircle{
    background: rgb(74, 74, 239);
    left:48rem;
    top:0rem;
}
.pinkCircle{
    background-color: var(--orange);
    left:49rem;
    top:8rem;
}
.w-blur{
 top:-1rem;
 left:50rem
}

@media screen and (min-width: 481px)and (max-width : 1180px){
    .works{
        flex-direction: column;
        height:40rem;
        padding:10px;
        gap:5rem;
    }
    .w-right{
        transform:scale(0.8);
        position:relative;
        left:20rem
    }
    .w-maincircle{
        position: relative;
        margin-left: -38rem;
        top:-2rem
    } 
    .w-secCircle{
        position:absolute;
    }
    .blueCircle{
        left:-12rem;
        top:-3rem
    }
    .pinkCircle{
        left:-12rem;
        top:3rem
    }
    
}

