.servicess{
    padding:0 3rem 0 3rem;
    display:flex;
    height: 70vh;
    margin-bottom: 13rem;
    margin-top: 9rem;
}
.awesome{
    display: flex;
    flex-direction: column;
    position: relative;
}
.awesome>:nth-child(1){
    color:var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}
.awesome>:nth-child(2){
    color:var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}
.awesome>:nth-child(3){
    color:white;
    font-size: 14px;
    margin-top: 1rem;
}
.s-button{
    width:8rem;
    margin-top: 1rem;
    height:1.5rem;
    text-align: center;
}
.cardses{
    position:relative;
}
.cardses>*{
    position:absolute
}

.s-blur{
    top:18rem;
    left:-18rem
}
@media screen and (min-width:100px) and (max-width : 480px){
    .servicess{
        margin-top: 0px;
        flex-direction: column;
        gap:5rem;
        height:68rem;
        padding:0
    }
    .cardses{
        margin-top:-2rem;
        display: flex;
        flex-direction: column;
        gap:17rem;
    }
    .cardses>*{
        position: static;
        padding:2rem;
        margin-left:2rem
    }
}
@media screen and (min-width : 500px) and (max-width : 1180px){
    .servicess{
        margin-top: 0px;
        /* flex-direction: column; */
        gap:5rem;
        height:20rem;
        padding:0;
        height:40rem
    }
    .cardses{
      margin-left:-70%;
     margin-top:30%
    }
}
