.c-right{
    height:20rem;
    width: 20rem;
    align-items: center;
}
.user{
    padding:0.3rem;
    height:3rem;
    width:22rem;
    border:3px solid var(--orange);
    border-radius: 10px;
    margin-top: 2.5rem;
    font-size: medium;
}
.user:nth-child(3){
    height:5rem;
    font-size: medium;
}
.contact-form{
    position:relative;
    height:26rem;
    display:flex;
    justify-content: space-around;
}
.user:focus {
    outline: none;
  }
.footer{
    height:5rem;
    width:200%;
    margin-left:-10rem;
    background-color: var(--orange);
}  
.c-left{
    position: relative;
}
.cc-blur{
    top:10rem;
    left:22rem;
}
.contact-detail{
    height: 12rem;
    width:20rem;
    border-top-left-radius: 30px;
    border-bottom-right-radius: 30px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    background-repeat: no-repeat;
    margin-top:3rem;
    background-size: cover;
    color:white;
    padding:3%;
    background-image: url("https://images.unsplash.com/photo-1534536281715-e28d76689b4d?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8Y29udGFjdHxlbnwwfHwwfHw%3D&w=1000&q=80");
}
@media screen and (max-width : 480px){
    .contact-form{
        flex-direction: column;
        height:30rem;
        gap:2rem
    }
    .c-left{
        margin-top: -8rem;
    }   
    .c-right{
        margin-top: -15rem;
        transform: scale(0.8);
    }
    .awe{
        top: 15rem;
    }
    .contact-detail{
        margin-top: 15rem;
        transform: scale(0.8);
    }
    .c-right{
        margin-top:-5rem;
        height:100rem
    }
    .footer{
        margin-top:8rem
    }
}
