.floatingdiv{
    display:flex;
    justify-content: space-around;
    box-shadow: var(--boxShadow);
    background: white;
    border-radius: 17px;
    align-items: center;
    padding:0px 28px 0px 0px;
    height: 4.5rem;
    width:11rem
}
.floatingdiv>span{
    font-family: sans-serif;
    font-size: 16px;
}
.floatingdiv>img{
    transform: scale(0.7);
    height:80%;
    width:40%
}