.a-wrapper{
    padding:0 3px 0 3px;
    height:75vh;
    align-items: center;
    justify-content: center;
    margin-top: 7rem;
    gap:2rem;
    position:relative;
    display:flex;
    flex-direction: column;}
.a-heading{
    align-self: start;
    margin-top: -10rem;
}
.a-heading>*{
    font-size: 2rem;
    font-weight: bold;
}
.a-heading>span:nth-child(2){
    color:var(--orange)
}
.mySelf{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding:2rem;
    width:28rem;
    height:10rem;
    background: rgba(255, 255, 255, 0.26);
    border:7px solid var(--orangeCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
}
.mySelf>img{
    width:8rem;
    height:8rem;
    border-radius: 100%;
}
.a-wrapper .swiper{
    width:100%;
    height:70%;
}
.a-wrapper .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
}
.a-wrapper .swiper-pagination-bullet-active{
    background: var(--orange);
}
.a-blur{
    top:13rem;
    left:5rem;
}
@media screen and (max-width : 1180px){
    .a-wrapper{
        height:40rem
    }
}

