.g-right>img{
     margin-top: 0px;
}
.g-right>img:nth-child(1){
    width:80%;
    align-items: center;
    margin-left:10%;
    margin-top:0px;
}
.g-right>img:nth-child(2){
    transform:scale(0.5);
    margin-top:-12vh;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.box{
    display:flex;
    justify-content: space-between;
    position:relative
}
.g-blur{
 position:absolute;

}
@media screen and (min-width:100px) and (max-width : 480px){
    .g-right>img:nth-child(2){
        margin-top:-3rem;
    }
    .g-right>img{
        transform:scale(1.8);
    }
    .box{
        margin-bottom: 2rem;
    }
}
@media screen and (min-width : 500px) and (max-width : 1180px){
    .g-right>img:nth-child(2){
        margin-top:-3rem;
    }
    .g-right>img{
        transform:scale(1.8);
    }
    .box{
        margin-bottom: 2rem;
        
    }
    .g-right>img{
        transform: scale(0.8);
    }
}