.toggle{
    display: flex;
    justify-content: space-between;
    border:3px solid var(--orange);
    cursor: pointer;
    border-radius: 1rem;
    position: relative;
    padding:2px
}
.toggle>*{
    width:1rem;
    height:1rem;
    color:var(--orange)
}
.t-button{
    position: absolute;
    border-radius: 100%;
    background-color: var(--orange);
}
@media screen and (max-width : 480px){
  .toggle{
    margin-left:25%
  }
}